.index {
    display: flex;
    :global {
        .archive-list-page {
            display: flex;
            width: 100%;
            .archive-type-list {
                width: 200px;
                padding: 20px 0px;
                margin-right: 10px;
    
                .type-item {
                    display: flex;
                    width: 100%;
                    line-height: 22px;
                    font-size: 12px;
                    border: solid 1px var(--main-border-color);
                    border-radius: 8px;
                    margin: 10px 0px;
                    padding: 5px;
    
                    .type-title {
                        width: calc(100% - 60px);
                        text-align: center;
                    }
                    .type-count {
                        width: 60px;
                        text-align: right;
                    }
    
                    &.active {
                        color: var(--main-active-color);
                        border-bottom: solid 2px var(--main-active-color);
                        border-right: solid 2px var(--main-active-color);
                    }
                }
    
    
            }
            .archive-list {
                border-left: solid 1px var(--main-border-color);
                width: calc(100% - 200px);
                padding: 8px 0px 8px 8px;
                .archive-list-content {
                    min-height: 500px;
                    .archive-item {
                        display: flex;
                        line-height: 40px;
        
                        &:hover {
                            border-bottom: solid 2px var(--main-active-color);
                        }
        
                        .type {
                            width: 150px;
                            .type-content {
                                font-size: 12px;
                                border: solid 1px var(--main-border-color);
                                padding: 0px 12px;
                                text-align: center;
                                line-height: 22px;
                                border-radius: 8px;
                                margin-top: 10px;
                            }
                        }
                        .title {
                            margin-left: 10px;
                            font-size: 16px;
                            width: calc(100% - 150px);
                        }
                        .time {
                            font-size: 14px;
                            color: var(--main-secondary-text-color);
                            width: 100px;
                            text-align: right;
                        }
                    }
                }
    
                .ant-empty {
                    padding-top: 10px;
                    min-height: 500px;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                }
            }
    
            .archive-page {
                padding-top: 10px;
                text-align: right;
            }
        }
    }
}