.index {
    background-color: var(--head-bg-color);
    width: 100%;
    height: 50px;
    padding: 10px 0px;

    :global {
        .head-content {
            width: var(--main-width);
            margin: auto;

            display: flex;

            .head-icon-title {
                width: 300px;
                display: flex;
                .head-icon {
                    img {
                        width: 40px;
                        height: 40px;
                    }
                }
    
                .head-title-content {
                    padding: 2px 8px;
                    .head-title {
                        font-size: 16px;
                        font-weight: bolder;
                        line-height: 20px;
                    }
        
                    .head-welcome {
                        font-size: 14px;
                        line-height: 20px;
                    }
                }
            }
            
    
            .head-menu {
                width: calc(100% - 300px);
                display: flex;
                flex-direction: row-reverse;
            }
        }
    }
}