:root {
    // 主要内容区域宽度
    --main-width: 1200px;

    // 主要背景色
    --main-bg-color: #222222;
    // 主要内容区域背景色
    --main-content-bg-color: #1b1b1b;

    // 主要边框色
    --main-border-color: #333333;
    // 主要文本颜色
    --main-text-color: #CCCCCC;
    // 次要文本颜色
    --main-secondary-text-color: #888888;
    --main-secondary-text-color: #888888;

    // 主要活动文本颜色
    --main-active-color: #4d85ff;
    

    // 页头背景色
    --head-bg-color: #222222;
    // 页脚背景色
    --foot-bg-color: #222222;    
}

html, body {
    background-color: var(--main-bg-color);
}

html, body, * {
    color: var(--main-text-color);
    padding: 0px;
    margin: 0px;
}

.wt-text-button {
    background-color: transparent;
    border: none;
    line-height: 22px;
    cursor: pointer;

    &:hover, &.active {
        color: var(--main-active-color);
    }

    &.active {
        color: var(--main-active-color);
        border-bottom: solid 2px var(--main-active-color);
    }

    &.active-button {
        > div:first-child {
            color: var(--main-border-color);
            background-color: var(--main-active-color);
            border: solid 1px var(--main-active-color);
        }
    }
}

.more-hide {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}