.index {
    padding: 20px;
    :global {
        .archive-content {
            padding: 8px 0px;
            .title {
                padding-top: 10px;
                font-size: 22px;
                font-weight: bold;
            }
            .time {
                padding-top: 5px;
                font-size: 14px;
                color: var(--main-secondary-text-color);
            }
            .steam-review, .quest-review {
                border: solid 1px var(--main-text-color);
                border-radius: 10px;
                padding: 10px;
                margin: 10px 0px;
                font-size: 12px;
                .field-item {
                    display: flex;
                    .label {
                        width: 100px;
                        text-align: right;
                    }
                }
            }
            .content {
                padding: 20px 0px;
                line-height: 30px;

                // 防止图片宽度超出
                img {
                    max-width: 100%;
                    object-fit: cover;
                }
            }
        }
    }
}